import s from './TypographyDescr.module.sass';

export default function TypographyDeskTop () {

const data = [
   {title: ' H1, 48/56, bold'},
   {title: ' H2, 32/40, bold'},
   {title: ' H3, 24/32, medium'},
   {title: ' H4, 18/28, medium'},
   {title: ' P_Lead, 24/32, regular'},
   {title: '  P_18, 18/28, regular'},
   {title: '  P_16, 16/24, regular'},
   {title: '  P_14, 14/20, regular'}
]

    return (
      <>
         { data.map((e, i) => 
         
               <div key={i} className={s.typography__descr__item__title}>
                  {e.title}
               </div>
            )
         }
      </>
   )

}